<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click="decreaseAmount">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="currentProductAmount">

    <button type="button" aria-label="Добавить один товар" @click="increaseAmount">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>

</template>

<script>
export default {
  data() {
    return {
      currentProductAmount: 0,
    };
  },
  props: ['productAmount'],
  methods: {
    increaseAmount() {
      this.currentProductAmount += 1;
      return this.$emit('update:productAmount', this.currentProductAmount);
    },
    decreaseAmount() {
      if (this.currentProductAmount > 0) {
        this.currentProductAmount -= 1;
      } else {
        this.currentProductAmount = 0;
      }
      return this.$emit('update:productAmount', this.currentProductAmount);
    },
  },
  mounted() {
    this.currentProductAmount = this.productAmount;
  },
  watch: {
    currentProductAmount() {
      this.$emit('update:productAmount', this.currentProductAmount);
    },
  },
};
</script>
