<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <!-- <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a> -->
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ products.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <div v-if="cartLoading">Загрузка корзины...</div>
            <div v-else-if="cartLoadingFailed">Ошибка загрузки корзины</div>
            <div v-else>
              <CartItem v-for="item in products" :key="item.productId" :item="item"/>
            </div>

          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit" :disabled="!products.length">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem.vue';

export default {
  data() {
    return {
      cartLoading: false,
      cartLoadingFailed: false,
    };
  },
  filters: {
    numberFormat,
  },
  components: { CartItem },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  methods: {
    loadCartItems() {
      this.cartLoading = true;
      this.cartLoadingFailed = false;
      clearTimeout(this.loadCartTimer);
      this.loadCartTimer = setTimeout(() => {
        this.$store.dispatch('loadCart')
          .catch(() => { this.cartLoadingFailed = true; })
          .then(() => { this.cartLoading = false; });
      }, 500);
    },
  },
};
</script>
